import Link from "next/link";
import { ReactNode, MouseEvent } from "react";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "@/lib/utils";

const buttonVariants = cva(
  "inline-flex items-center justify-center rounded-xl font-medium transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 disabled:pointer-events-none font-[var(--font-inter)]",
  {
    variants: {
      variant: {
        default: "bg-[#6467F2] text-white hover:bg-[#5456D9] focus:ring-[#6467F2]",
        outline: "border border-[#6467F2] text-[#6467F2] hover:bg-[#6467F2] hover:text-white focus:ring-[#6467F2]",
        ghost: "text-[#6467F2] hover:bg-[#6467F2]/10 focus:ring-[#6467F2]",
      },
      size: {
        default: "h-10 px-4 text-sm",
        sm: "h-8 px-3 text-xs",
        lg: "h-12 px-6 text-base",
      },
    },
    defaultVariants: {
      variant: "default",
      size: "default",
    },
  }
);

interface BaseButtonProps extends VariantProps<typeof buttonVariants> {
  children: ReactNode;
  className?: string;
  onClick?: (e: MouseEvent<HTMLElement>) => void;
}

interface ButtonWithHref extends BaseButtonProps {
  href: string;
}

interface ButtonWithoutHref extends BaseButtonProps {
  href?: never;
  type?: "button" | "submit" | "reset";
  disabled?: boolean;
}

type ButtonProps = ButtonWithHref | ButtonWithoutHref;

export function Button({
  children,
  href,
  variant,
  size,
  className,
  onClick,
  ...props
}: ButtonProps) {
  const buttonClasses = cn(buttonVariants({ variant, size }), className);

  if (href) {
    return (
      <Link
        href={href}
        className={buttonClasses}
        onClick={onClick}
      >
        {children}
      </Link>
    );
  }

  return (
    <button
      className={buttonClasses}
      onClick={onClick}
      {...(props as { type?: "button" | "submit" | "reset"; disabled?: boolean })}
    >
      {children}
    </button>
  );
}
